<template>
    <div class="btn-dotted btn-dotted_default" @click="insertSectionText">
        Вставить как текст
    </div>
</template>

<script>
export default {
    name: "InsertSectionText",
    methods: {
        insertSectionText(){
            try {
                let result = prompt('Введите JSON');
                let json = JSON.parse(result)

                this.$emit("insert-section-text", json)
            } catch (e) {
                alert("Произошла ошибка!")
            }
        }
    }
}
</script>

